@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Open Sans", sans-serif;
}

body {
  margin: 0;
  background: #dfdfdf;
}

.slide-in-left-enter {
  transform: translateX(-100%);
}

.slide-in-left-enter-active {
  transform: translateX(0);
  opacity: 1;
  transition: all 200ms;
}

.slide-in-left-exit {
  transform: translateX(0%);
  opacity: 1;
}

.slide-in-left-exit-active {
  transform: translateX(-100%);
  opacity: 0;
  transition: all 200ms;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* style for date picker section */
.Date-picker-wrapper {
  text-align: center;
  margin: auto;
  width: 47%;
}
.Date-picker {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}
.startdate,
.enddate {
  padding: 10px;
  text-transform: capitalize;
  font-weight: bolder;
}
.Date-picker .to {
  margin: 10px;
  color: gray;
  font-weight: bolder;
}
.filter-btn {
  padding: 10px;
  width: 150px;
  margin-left: 10px;
  font-weight: 900;
  background-color: white;
  color: gray;
  border: 1px solid grey;
}
@media screen and (max-width: 900px) {
  .Date-picker {
    display: block;
  }
  .filter-btn {
    margin-left: 0;
    margin: 10px 0px;
    width: 135px;
  }
}

.card {
  position: relative;
  margin: 0;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 6px;
  padding: 1rem;
  overflow: hidden;
  background: white;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.75);
  z-index: 10;
}

.modal {
  z-index: 100;
  position: fixed;
  /* top: 22vh; */
  left: 10%;
  width: 80%;
  background: white;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.26);
  border-radius: 8px;
}

.modal__header {
  width: 100%;
  padding: 1rem 0.5rem;
  background: #15253b;
  color: white;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
}

.modal__header h2 {
  margin: 0.5rem;
}

.modal__content {
  padding: 1rem 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
}

@media (min-width: 768px) {
  .modal {
    left: calc(50% - 20rem);
    width: 40rem;
  }
}

.modal-enter {
  transform: translateY(-10rem);
  opacity: 0;
}

.modal-enter-active {
  transform: translateY(0);
  opacity: 1;
  transition: all 200ms;
}

.modal-exit {
  transform: translateY(0);
  opacity: 1;
}

.modal-exit-active {
  transform: translateY(-10rem);
  opacity: 0;
  transition: all 200ms;
}

.btn {
  background-color: #15253be1;
  color: white;
  font-size: 18px;
  font-weight: bold;
  padding: 16px 60px;
  cursor: pointer;
  text-decoration: none;
  display: inline-block;
}

.btn:hover {
  background-color: #15253bb4;
}

.button:disabled,
.button:hover:disabled,
.button:active:disabled {
  background: #ccc;
  color: #979797;
  border-color: #ccc;
  cursor: not-allowed;
}

@media (max-width: 600px){
  .btn {
    font-size: 14px;
    padding: 10px 15px;
  }
}

p {
    font-size: 1.3em;
    text-align: center;
}
.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.comment-box {
  height: 230px;
  overflow-y: auto;
  overflow-x: hidden;
  background-color: gainsboro;
}
.comments {
  display: block;
  /* padding: 20px; */
  /* background-color: rebeccapurple; */
  margin-top: 10px;
  /* box-shadow: 1px 2px 2px black; */
  border-radius: 0px 10px 10px 10px;
}
.comments p {
  /* background-color: blanchedalmond; */
