}
.user {
  text-align: left;
  /* background-color: cornsilk; */
  display: inline;
  border-radius: 0px 10px 10px 10px;
  font-weight: 900;
}
.message {
  text-align: left;
  position: relative;
  left: 16px;
  /* margin-top: 100px; */
  border-radius: 0px 10px 10px 10px;

